import React from 'react';
import { useDispatch } from 'react-redux';
import { addItems } from '../../Redux/BasketSlise';
export default function PizzaBlok({ id, prise, imageUrl, title, sizes, types }) {
  const [currentNumber, setcurrentNumber] = React.useState(0);
  const NamePizza = ['', ''];

  const [activeType, setActiveType] = React.useState(0);
  const [activeSize, setactiveSize] = React.useState(0);
  const dispatch = useDispatch();

  const addItemsOnBasket = () => {
    const items = {
      imageUrl,
      title,
      prise,
      size: sizes[activeSize],
      type: NamePizza[activeType],
      id,
    };

    dispatch(addItems(items));
  };

  return (
    <div className="pizza-block-wrapper">
      <div className="pizza-block">
        <img className="pizza-block__image" src={imageUrl} alt="Pizza" />
        <h4 className="pizza-block__title">{title}</h4>
        <div className="pizza-block__selector">
          <ul>
            {types.map((typesId) => (
              <li
                key={typesId}
                onClick={() => setActiveType(typesId)}
                className={activeType === typesId ? 'active' : ''}>
                {NamePizza[typesId]}
              </li>
            ))}
          </ul>
          <ul>
            {sizes.map((sizePizza, i) => (
              <li
                key={i}
                onClick={() => setactiveSize(i)}
                className={activeSize === i ? 'active' : ''}>
                {' '}
                {sizePizza}
              </li>
            ))}
          </ul>
        </div>
        <div onClick={addItemsOnBasket} className="pizza-block__bottom">
          <div className="pizza-block__price">
            <span> {prise}  </span>
          </div>
          <button
            onClick={() => setcurrentNumber(currentNumber + 1)}
            className="button button--outline button--add">
            <svg
              width="12"
              height="12"
              viewBox="0 0 12 12"
              fill="none"
              xmlns="http://www.w3.org/2000/svg">
              <path
                d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                fill="white"
              />
            </svg>
            <span></span>
            <i>{currentNumber}</i>
          </button>
        </div>
      </div>
    </div>
  );
}
